// components/TabsSection.jsx
"use client";
import { useState } from "react";
import styles from "@/styles/TabsSection.module.css";

const TabsSection = () => {
  const [activeTab, setActiveTab] = useState(0);
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  const tabs = [
    {
      title: "Olivia",
      heading: "Olivia",
      blurb: "Princess ",
      imgSrc: "/images/whitedress.jpg",
      alt: "White Dress",
    },
    {
      title: "Emma",
      heading: "Emma",
      blurb: "Model",
      imgSrc: "/images/Emma.jpg",
      alt: "White Dress",
    },
    {
      title: "Ava",
      heading: "Ava",
      blurb: "Model",
      imgSrc: "/images/ava.jpg",
      alt: "White Dress",
    },
    {
      title: "Mia",
      heading: "Mia",
      blurb: "Model",
      imgSrc: "/images/mia.jpg",
      alt: "White Dress",
    },
    {
      title: "Amelia",
      heading: "Amelia",
      blurb: "Model",
      imgSrc: "/images/Amelia-new.jpg",
      alt: "White Dress",
    },
  ];

  const handleTabClick = (index) => {
    setActiveTab(index);
    setIsDropdownOpen(false);
  };

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
  };

  const currentTab = tabs[activeTab];

  return (
    <section
      className={`tabsSection bg-black overflow-visible ${styles.tabsSection}`}
    >
      <div className="container">
        {/* Desktop Tabs */}
        <div className={`tabHeader ${styles.tabHeader}`}>
          {tabs.map((tab, index) => (
            <button
              key={index}
              className={`${styles.tabButton} ${
                activeTab === index ? styles.active : ""
              }`}
              onClick={() => handleTabClick(index)}
            >
              {tab.title}
            </button>
          ))}
        </div>

        {/* Mobile Dropdown */}
        <div className={styles.mobileDropdown}>
          <button className={styles.dropdownToggle} onClick={toggleDropdown}>
            {tabs[activeTab].title}
            <span className={styles.arrow}></span>
          </button>

          {isDropdownOpen && (
            <div className={styles.dropdownMenu}>
              {tabs.map((tab, index) => (
                <button
                  key={index}
                  className={`${styles.dropdownItem} ${
                    activeTab === index ? styles.active : ""
                  }`}
                  onClick={() => handleTabClick(index)}
                >
                  {tab.title}
                </button>
              ))}
            </div>
          )}
        </div>

        {/* Tab Content */}
        <div className={`tabContent ${styles.tabContent}`}>
          <div className="inner-wrap">
            <h3 className="text-white">{currentTab.heading}</h3>
            <p className="text-white pt-[15px]">{currentTab.blurb}</p>
            <div className="bg-wrap h-[1000px] md:h-[100%] w-full overflow-hidden pt-[50px] lg:pt-[20px]">
              <img
                className="h-[100%] w-[100%] relative object-cover"
                src={currentTab.imgSrc}
                alt={currentTab.alt}
                loading="lazy"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default TabsSection;
